/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'ArcadeClassic';
  src: url("/fonts/ArcadeClassic.eot");
  src: url("/fonts/ArcadeClassic.eot?#iefix") format("embedded-opentype"), url("/fonts/ArcadeClassic.woff2") format("woff2"), url("/fonts/ArcadeClassic.woff") format("woff"), url("/fonts/ArcadeClassic.ttf") format("truetype"), url("/fonts/ArcadeClassic.svg#ArcadeClassic") format("svg");
  font-weight: normal;
  font-style: normal; }

header {
  background: black;
  padding: 20px; }
  header p {
    color: white;
    text-align: center;
    font-size: 40px;
    font-family: 'ArcadeClassic';
    font-weight: normal;
    font-style: normal;
    word-spacing: 30px; }

html, body {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  background-color: darkgray; }

img {
  width: 50px;
  height: 50px; }

.game {
  display: flex;
  flex-direction: column; }
